
import React from 'react'
import Sidebar from '../../Components/Sidebar/Sidebar'
import Charts from '../../Components/Charts/Charts'

import "./home.scss"


const Home = () => {
  return (
    <div className="home">
      <Sidebar />
      <div className="homeContainer">
        <div className='charts'>
          <Charts  title="Analytics" aspect={3 / 1}/>
        </div>
      </div>
    </div>
  )
}

export default Home